import { createRouter, createWebHistory } from 'vue-router'
import store from '../store'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Trading from '../views/Trading.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { 
      title: 'Ìôà - Ï£ºÏãù ÏûêÎèôÎß§Îß§ ÏÇ¨Ïù¥Ìä∏',
      requiresAuth: false 
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { 
      title: 'Î°úÍ∑∏Ïù∏ - Ï£ºÏãù ÏûêÎèôÎß§Îß§ ÏÇ¨Ïù¥Ìä∏',
      requiresAuth: false 
    }
  },
  {
    path: '/trading',
    name: 'Trading',
    component: Trading,
    meta: { 
      title: 'ÏûêÎèôÎß§Îß§ - Ï£ºÏãù ÏûêÎèôÎß§Îß§ ÏÇ¨Ïù¥Ìä∏',
      requiresAuth: true 
    }
  },
  {
    path: '/auth/callback',
    name: 'AuthCallback',
    component: () => import('../views/AuthCallback.vue'),
    meta: { 
      title: 'Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨ Ï§ë...',
      requiresAuth: false 
    }
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import('../views/NotFound.vue'),
    meta: { 
      title: 'ÌéòÏù¥ÏßÄÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§',
      requiresAuth: false 
    }
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  mode: 'hash', // Î∞òÎìúÏãú hash Î™®ÎìúÎ°ú

  routes
})

// ÎùºÏö∞ÌÑ∞ Í∞ÄÎìú
router.beforeEach((to, from, next) => {
  // ÌéòÏù¥ÏßÄ ÌÉÄÏù¥ÌãÄ ÏÑ§Ï†ï
  document.title = to.meta.title || 'Ï£ºÏãù ÏûêÎèôÎß§Îß§ ÏÇ¨Ïù¥Ìä∏'
  
  // Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌïú ÌéòÏù¥ÏßÄ Ï≤¥ÌÅ¨
  if (to.meta.requiresAuth) {
    const isAuthenticated = store.getters['auth/isAuthenticated']
    
    if (!isAuthenticated) {
      // Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Î¶¨Îã§Ïù¥Î†âÌä∏ÌïòÍ≥† ÏõêÎûò Í∞ÄÎ†§Îçò ÌéòÏù¥ÏßÄ Ï†ïÎ≥¥ Ï†ÄÏû•
      next({
        name: 'Login',
        query: { redirect: to.fullPath }
      })
      return
    }
  }
  
  // Ïù¥ÎØ∏ Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©ÏûêÍ∞Ä Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÏóê Ï†ëÍ∑ºÌïòÎäî Í≤ΩÏö∞
  if (to.name === 'Login' && store.getters['auth/isAuthenticated']) {
    next({ name: 'Home' })
    return
  }
  
  next()
})

export default router